.project-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #c6c6c6;
}

.content-wrapper-one {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100%;
    background: url('../../images/upperborder.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.content-wrapper-two {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background: url('../../images/blackBackground.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

.content-wrapper-three {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100%;
    background: url('../../images/lowerborder.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.project-section  {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: transparent;
    
}

.project-section.tools {
    margin-top: -150px;
    color: #c6c6c6;
}

.project-section.design {
    color: #c6c6c6;
    text-align: center;
    max-width: 800px;
    margin-top: 100px;
}

.project-section.wip {
    margin-bottom: 100px;
    margin-top: 150px;
    max-width: 800px;
}

.project-section.dev {
    margin-top: 100px;
    max-width: 800px;
}

.project-split-section {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: transparent;
    gap: 60px;
    margin-top: 50px;
}

.git-link {
    font-size: 25px;
    display: flex;
    gap: 20px;
    margin-top: 60px;
    color: black;
    text-decoration: none;
    font-weight: bold;
}

.git-link:hover {
    text-decoration: underline;
}

/* text elements */
.project-title {
    font-size: 120px;
    font-weight: bold;
    margin-top: 100px;
}

.project-desc {
    font-size: 36px;
    font-weight: bold;
    font-style: italic;
    max-width: 800px;
    margin-top: 50px;
    text-align: center;
}

.project-subtitle {
    font-size: 50px;
    font-weight: bold;
    color: #c6c6c6;
}

.project-subtitle-section {
    font-size: 35px;
    color: #c6c6c6;
    font-style: italic;
    font-weight: bold;
    margin-top: 30px;
    margin-bottom: 10px;
}

.project-main-text {
    font-size: 25px;
    color: #c6c6c6;
    text-align: center;
}

.project-split-text {
    font-size: 25px;
    margin-left: 10px;
    color: #c6c6c6;
    max-width: 500px;
}

.project-main-text.photo-text {
    max-width: 800px;
    margin-top: 100px;
}

.project-main-text.fix-text {
    max-width: 800px;
}

/* image elements */

#app-demo {
    max-width: 400px;
}

#app-design {
    margin-top: 50px;
}

.tip-img {
    max-width: 800px;
}

.tip-img-mobile {
    max-height: 580px;
}

.git-icon {
    font-size: 40px;
}

.design-img-one {
    max-width: 800px;
}

.design-img-two {
    max-height: 600px;
}

.heuristics {
    max-width: 800px;
}

.design-laws {
    max-width: 800px;
}

.design-prototype {
    max-height: 800px;
    margin-top: 50px;
}

.design-data {
    max-width: 500px;
    margin-top: -100px;
    margin-bottom: 35px;
}

@media only screen and (max-width: 767px) {
    .project-title {
        font-size: 45px;
        margin-top: 50px;
        text-align: center;
    }
    .project-desc {
        font-size: 25px;
        max-width: 500px;
        margin-top: 20px;
    }

    .project-section.tools {
        margin-top: -450px;
        max-width: 500px;
    }

    .project-section.design {
        max-width: 500px;
    }
    
    .project-section.dev {
        max-width: 500px;
    }

    .project-section.wip {
        margin-top: 100px;
        max-width: 500px;
    }

    .project-split-section {
        flex-direction: column;
        text-align: center;
        gap: 60px;
        margin-top: 50px;
    }

    #app-design {
        max-width: 500px;
    }

    .tip-img {
        max-width: 500px;
    }
    .tip-img-mobile {
        max-width: 400px;
    }

    .design-img-one {
        max-width: 500px;
    }
    
    .design-img-two {
        max-height: 400px;
    }
    
    .heuristics {
        max-width: 500px;
    }
    
    .design-laws {
        max-width: 500px;
    }
    
    .design-prototype {
        max-height: 400px;
        margin-top: 50px;
    }
    
    .design-data {
        max-width: 400px;
        margin-top: -80px;
        margin-bottom: 35px;
    }

    .project-main-text.photo-text {
        max-width: 800px;
        margin-top: 50px;
    }
 }
 

 
 @media only screen and (max-width: 1023px) {
    .project-title {
        font-size: 70px;
        margin-top: 50px;
    }
    .project-desc {
        font-size: 25px;
        max-width: 500px;
        margin-top: 20px;
    }

    .project-section.tools {
        margin-top: -450px;
        max-width: 600px;
    }

    .project-section.design {
        max-width: 600px;
    }
    
    .project-section.dev {
        max-width: 600px;
    }

    .project-section.wip {
        margin-top: 100px;
        max-width: 600px;
    }

    .project-split-section {
        flex-direction: column;
        text-align: center;
        gap: 60px;
        margin-top: 50px;
    }

    #app-design {
        max-width: 500px;
    }
 
     
 
 }