.about-wrapper {
    background-color: #c6c6c6;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 200px;
    background: url('../../images/blackBackground.jpg');
    background-size: 100%;
}

.page-title {
    color: #c6c6c6;
    font-size: 64px;
    font-weight: bold;
}

.section-header {
    color: #c6c6c6;
    text-align: center;
    font-size: 64px;
}

.sub-head {
    font-size: 50px;
    font-weight: bold;
    color: #c6c6c6;
}

.language-sub-head {
    font-size: 35px;
    font-weight: bold;
    margin-top: 50px;
    margin-bottom: 20px;
    color: #c6c6c6;
}

.tech-sub-head {
    font-size: 35px;
    font-weight: bold;
    margin-top: 70px;
    margin-bottom: 20px;
    color: #c6c6c6;
}

.split-paragraph {
    display: flex;
    max-width: 800px;
    margin-top: 150px;
    height: 400px;
    gap: 60px;
    margin-bottom: 100px;
}

.column-one {
    justify-content: center;
    align-items: center;
    max-width: 300px;
    padding: 40px;
}

.column-two {
    background-color: #c6c6c6;
    border-radius: 15px;
    max-width: 400px;
    padding: 40px;
}

#about-paragraph-one {
    max-width: 700px;
    text-align: center;
    font-size: 25px;
    margin-top: 50px;
    color: #c6c6c6;
}

#about-paragraph-two {
    font-size: 25px;
    color: #c6c6c6;
    text-align: center;
    max-width: 600px;
    background: transparent;
    font-weight: bold;
}

#about-paragraph-three {
    font-size: 25px;
    text-align: center;
    max-width: 300px;
    color: black;
    background: transparent;
    margin-top: 50px;
}

#technologies-list {
    color:#c6c6c6;
    text-align: center;
    font-size: 25px;
}


@media only screen and (max-width: 767px) {
    #about-paragraph-one {
        max-width: 600px;
        text-align: center;
        font-size: 25px;
        margin-top: 50px;
        color: #c6c6c6;
    }

    .split-paragraph {
        display: flex;
        flex-direction: column;
        max-width: 600px;
        margin-top: 100px;
        gap: 10px;
    }

    .sub-head {
        margin-top: 150px;
    }

    #about-paragraph-three {
        margin-top: 10px;
    } 
}


@media only screen and (min-width: 768px) and (max-width: 1023px) {
    #about-paragraph-one {
        max-width: 600px;
        text-align: center;
        font-size: 25px;
        margin-top: 50px;
        color: #c6c6c6;
    }

    .split-paragraph {
        display: flex;
        flex-direction: column;
        max-width: 600px;
        margin-top: 100px;
        gap: 10px;
    }

    .sub-head {
        margin-top: 150px;
    }

    #about-paragraph-three {
        margin-top: 10px;
    } 
}