.portfolio-wrapper{
    height: 900px;
    display: flex;
    background: url('../../images/lowerborder.png');
    background-size: cover;
    flex-direction: column;
    align-items: center;
    padding-top: 200px;
    background-color: #c6c6c6;
}

.projects-wrapper {
    max-width: 100vw;
    margin-top: 200px;
    height: 500px;
    display: flex;
    flex-direction: row;
    gap: 20px;
    background: transparent;
}

.project-card {
    width: 250px;
    height: 250px;
    background-color: transparent;
    perspective: 1000px;
}

.project-photo {
    width: 250px;
    height: 250px;
}

.portfolio-title {
    color: #c6c6c6;
    font-size: 64px;
    font-weight: bold;
}

.card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    background: linear-gradient(180deg, #D8D8D8 0%, rgba(120, 120, 120, 0.72) 100%);
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
}

.project-card:hover .card-inner{
    transform:rotateY(180deg);
}

.card-front, .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.card-back {
    transform: rotateY(180deg);
    margin-top: 10px;
}

.card-title {
    color: black;
    margin-top: 20px;
    margin-left: 5px;
    font-size: 25px;
    font-weight: bold;
    text-align: center;
}

.card-desc {
    margin-left: 10px;
    color: black;
    margin-top: 20px;
    font-size: 20px;
    text-align: center;
}


@media only screen and (max-width: 767px) {
   .portfolio-title {
       margin-top: -100px;
   }

   .projects-wrapper {
        height: 700px;
        flex-direction: column;
        gap: 30px;
    }

    .project-photo {
        width: 200px;
        height: 200px;
        padding: 20px;
    }
}



@media only screen and (min-width: 768px) and (max-width: 1023px) {
    .projects-wrapper {
        max-width: 100vw;
        margin-top: 100px;
        height: 700px;
        display: flex;
        flex-direction: column;
        gap: 30px;
        background: transparent;
    }

    .project-photo {
        width: 200px;
        height: 200px;
        padding: 20px;
    }

}