.menu-container{
    height: 50px;
    position:sticky;
    top: 0;
    z-index: 10;
    display: flex;
    justify-content: right;
    align-items: center;
    background-color: #c2c2c2;
}

.menu-item {
    margin-right: 60px;
    font-size: 20px;
    color: black;
}

.menu-item:hover {
    cursor: pointer;
}

.links.active{
    border-bottom: 3px solid black;
}