.contact-wrapper{
    background-color: #c6c6c6;
}

.contact-container {
    display: flex;
    gap: 100px;
    justify-content: center;
    align-items: center;
    padding-top: 100px;
    padding-bottom: 100px;
}

.contact-bttn {
    width: 150px;
    position: relative;
}

.contact-icon{
    width: 150px;
    height: 100px;
    color:rgba(0, 0, 0, 1);
}

.contact-text {
    display: none;
    text-align: center;
    width: 150px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    font-weight: bold;
    font-size: 30px;
    color: black;
    margin-top: 20px;
}

.contact-bttn :hover{
    cursor: pointer;
}

.contact-bttn:hover > .contact-icon {
    color: rgba(0, 0, 0, 0.33);
}

.contact-bttn:hover > .contact-text {
    display: block;
}


@media only screen and (max-width: 767px) {
    .contact-container {
        gap: 5px;
    }
    
  }

  
  @media only screen and (min-width: 768px) and (max-width: 1023px) {
    .contact-container {
        gap: 15px;
    }
  }