.menu-wrapper {
    height: 50px;
    position:sticky;
    top: 0;
    display: flex;
    align-items: center;
    padding-left: 60px;
    gap: 60px;
    padding-top: 10px;
    background-color:#c6c6c6;
  
}

.menu-link {
    color: black;
    font-size: 25px;
    text-decoration: none;
    border-bottom: 2px solid transparent;
    font-weight: bold;
    vertical-align: middle;
}

.menu-icon {
    width: 30px;
    height: 30px;
    margin-right: 5px;
}

.back-link {
    display: flex;
}

.back-link:hover .menu-link {
    border-bottom: 3px solid black;
    cursor: pointer;
}
