.home-wrapper {
    height: 100vh;
    background: url('../../images/upperborder.png');
    background-size: cover;
    display: flex;
    justify-content: center;
    background-color: #c6c6c6;
}

.text-wrapper {
    margin-top: 300px;
    display: flex;
    background: transparent;
    list-style-type: none;
}

.title-text {
    font-size: 200px;
    background: transparent;
    font-weight: bold;
}


@media only screen and (max-width: 640px) {
    .text-wrapper {
        width: 500px;
        margin-top: 200px;
    }

    .title-text {
        font-size: 70px;
    }
  }

  
@media only screen and (max-width: 767px) {
    .text-wrapper {
        width: 550px;
        margin-top: 200px;
    }

    .title-text {
        font-size: 60px;
    }
  }

  
  @media only screen and (min-width: 768px) and (max-width: 1023px) {
    .text-wrapper {
        max-width: 580px;
        margin-top: 200px;
    }

    .title-text {
        font-size: 90px;
    }
  }